import React, { FunctionComponent } from 'react';
import ClassNames from './RichText.module.scss';
import { documentToHtmlString } from '@contentful/rich-text-html-renderer';

interface Props {
    className?: string;
    data: any;
}

const RichText: FunctionComponent<Props> = ({ className, data }) => {
    return <div className={ClassNames.RichText} dangerouslySetInnerHTML={{ __html: documentToHtmlString(data) }} />;
};

export default RichText;
