import React, { FunctionComponent } from 'react';
import classNames from 'classnames';
import ClassNames from './SplashSection.module.scss';
import RichText from '../content-types/RichText/RichText';

export interface CmsProps {
    sys: {
        id: string;
    };
    fields: {
        image: any;
        text: any;
        background1: string;
        background2: string;
        textColor1?: string;
        textColor2?: string;
    };
}

export enum AppearType {
    ACTIVE = 1,
    ACTIVE_NEXT,
    ACTIVE_PREV,
    WAS_ACTIVE,
    NONE,
}

interface Props {
    appearType: AppearType;
}

const SplashSection: FunctionComponent<Props & CmsProps> = props => {
    const fields = props.fields;
    return (
        <div className={classNames(ClassNames.SplashSection, ClassNames[AppearType[props.appearType]])}>
            <div className={ClassNames.area1} style={{ background: fields.background1, color: fields.textColor1 }}>
                <img className={ClassNames.img} src={fields.image.fields.file.url} />
            </div>
            <div className={ClassNames.area2} style={{ background: fields.background2, color: fields.textColor2 }}>
                <RichText className={ClassNames.text} data={fields.text} />
            </div>
        </div>
    );
};

export default SplashSection;
