import React, { createContext, FunctionComponent, useContext, useMemo, useState } from 'react';
import { ILocale } from '../../types/ILocale';

interface IAppConfigContext {
    locales: ILocale[];
    currentLocaleCode: string;
    slug: string;
    something: any;
    setSomething(hello: number): void;
}

interface Props {
    locales: ILocale[];
    currentLocaleCode: string;
    slug: string;
}

const Context = createContext<IAppConfigContext>(undefined!);

function useAppConfig() {
    return useContext(Context);
}

const AppConfigProvider: FunctionComponent<Props> = props => {
    /* 'something' is an example here to make the context changeable AND have memorized context, this way:
     * - no unnecessary rerender
     * - having props for context AND settable properties
     * - no class with getDerivedStateFromProps, although I don't think it's that bad
     *   (in contrast with the internet :), this prevents it and is shorter.
     **/
    const [something, setSomething] = useState(0);

    // memorized variable. if props and something don't change, same value is returned so no rerender trigger.
    const context = useMemo<IAppConfigContext>(() => {
        return {
            locales: props.locales,
            currentLocaleCode: props.currentLocaleCode,
            slug: props.slug,
            something,
            setSomething,
        };
    }, [props, something]);

    return <Context.Provider value={context}>{props.children}</Context.Provider>;
};

export { AppConfigProvider, useAppConfig };
